import { Component, OnInit } from '@angular/core';
import { DirektoriPelamarKeahlianService, DirektoriPegawaiKeahlianService } from 'src/app/sdk/hr/services';
import { Router } from '@angular/router';
import { NzMessageService } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';
import { PegawaiDetailService } from '../../../service/pegawai-detail.service';
import { Pegawai_keahlian } from 'src/app/sdk/hr/models';

@Component({
    selector: 'app-list',
    templateUrl: './list.component.html',
    styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private direktoriPegawaiKeahlianService: DirektoriPegawaiKeahlianService,
        private router: Router,
        private pegawaiDetailService: PegawaiDetailService,
        private nzMessageService: NzMessageService,
        private spinner: NgxSpinnerService,
    ) { }

    dataSkill: any [] = []
    dataBahasa: any [] = []
    toogleupload = false;
    toogleuploadSkill = false;

    id_pegawai: string = ''

    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data
            this.getKeahlian()
        })

    }

    toogleLanguageshow() {
        this.toogleupload = true;
    }

    toogleSkillshow() {
        this.toogleuploadSkill = true;
    }

    pegawai_keahlian: string = ''
    tingkat_keahlian: string = ''
    kategori_keahlian: string = 'bahasa'
    simpan() {
        let param: Pegawai_keahlian = {
            pegawai_keahlian: this.pegawai_keahlian,
            tingkat_keahlian: this.tingkat_keahlian,
            kategori_keahlian: this.kategori_keahlian,
            id_pegawai: this.id_pegawai,
        };
        this.direktoriPegawaiKeahlianService.postDirektoriPegawaiKeahlians(param).subscribe(
            data=> {
                this.getKeahlian()
                this.toogleupload = false
            }
        )
    }

    kepandaian: string = 'teknis'
    pegawai_keahlian2: string = ''
    tingkat_keahlian2: string = ''
    simpanSkill() {
        let param: Pegawai_keahlian = {
            pegawai_keahlian: this.pegawai_keahlian2,
            tingkat_keahlian: this.tingkat_keahlian2,
            kategori_keahlian: this.kepandaian,
            id_pegawai: this.id_pegawai,
        };
        this.direktoriPegawaiKeahlianService.postDirektoriPegawaiKeahlians(param).subscribe(
            data=> {
                this.getKeahlian()
                this.toogleuploadSkill = false
            }
        )
    }

    getKeahlian() {
        this.direktoriPegawaiKeahlianService.getDirektoriPegawaiKeahlians(JSON.stringify({ id_pegawai: this.id_pegawai })).subscribe(
            data => {
                this.dataSkill = data.data['teknis']
                this.dataBahasa = data.data['bahasa']
            }
        )
    }

    delete(id) {
        this.spinner.show()
        this.direktoriPegawaiKeahlianService
            .deleteDirektoriPegawaiKeahliansId(id)
            .subscribe(data => {
                this.getKeahlian()
                this.spinner.hide()
            })
    }

    batalDelete() {
        this.nzMessageService.info('click cancel')
    }



}
