import { Component, OnInit } from '@angular/core';
import { DirektoriPelamarKeahlianService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Pelamar_keahlian } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';
import { PegawaiDetailService } from '../../../service/pegawai-detail.service';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private direktoriPelamarKeahlianService: DirektoriPelamarKeahlianService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pegawaiDetailService: PegawaiDetailService,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
    ) {}

    id_pelamar: string = ''
    dataKeahlian: Pelamar_keahlian

    id_pelamar_keahlian: string = ''

    ngOnInit() {
        this.id_pelamar_keahlian = this.activatedRoute.snapshot.paramMap.get('id_pelamar_keahlian')
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pelamar = data
        })
        this.getData()
        this.form()
    }

    getData() {
        this.spinner.show()
        this.direktoriPelamarKeahlianService.getDirektoriPelamarKeahliansId(this.id_pelamar_keahlian).subscribe(
            data=> {
                this.dataForm.setValue(data.data)
                this.spinner.hide()
            }
        )
    }

    dataForm: FormGroupTypeSafe<Pelamar_keahlian>
    form() {
        this.dataForm = this.fb.group<Pelamar_keahlian>({
            pelamar_keahlian: new FormControl('', [Validators.required]),
            kategori_keahlian: new FormControl(''),
            sertifikat: new FormControl(''),
            tingkat_keahlian: new FormControl(''),
            id_dokumen: new FormControl(''),
            id_user: new FormControl(''),
            id_pelamar_keahlian: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
            id_pelamar: new FormControl(this.id_pelamar),
        })
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.dataKeahlian = this.dataForm.value
        this.direktoriPelamarKeahlianService
            .putDirektoriPelamarKeahliansId({id: this.id_pelamar_keahlian, body: this.dataKeahlian})
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/detail-pelamar', this.id_pelamar, 'keahlian', 'list'])
    }

}
