import { Pelamar_keahlian } from 'src/app/sdk/hr/models/pelamar-_keahlian';
import { Component, OnInit } from '@angular/core';
import { DirektoriPelamarKeahlianService } from 'src/app/sdk/hr/services';
import { Router } from '@angular/router';
import { PelamarServiceService } from '../../service/pelamar-service.service';
import { NzMessageService } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
    selector: 'app-list',
    templateUrl: './list.component.html',
    styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private direktoriPelamarKeahlianService: DirektoriPelamarKeahlianService,
        private router: Router,
        private pelamarServiceService: PelamarServiceService,
        private nzMessageService: NzMessageService,
        private spinner: NgxSpinnerService,
    ) { }

    dataSkill: any [] = []
    dataBahasa: any [] = []
    toogleupload = false;
    toogleuploadSkill = false;

    id_pelamar: string = ''

    ngOnInit() {
        this.pelamarServiceService.getIdPelamar().subscribe(data => {
            this.id_pelamar = data
            console.log('id pelamar paja tu ' + this.id_pelamar)
            this.getKeahlian()
        })

    }

    toogleLanguageshow() {
        this.toogleupload = true;
    }

    toogleSkillshow() {
        this.toogleuploadSkill = true;
    }

    pelamar_keahlian: string = ''
    tingkat_keahlian: string = ''
    kategori_keahlian: string = 'bahasa'
    simpan() {
        let param: Pelamar_keahlian = {
            pelamar_keahlian: this.pelamar_keahlian,
            tingkat_keahlian: this.tingkat_keahlian,
            kategori_keahlian: this.kategori_keahlian,
            id_pelamar: this.id_pelamar,
        };
        this.direktoriPelamarKeahlianService.postDirektoriPelamarKeahlians(param).subscribe(
            data=> {
                this.getKeahlian()
                this.toogleupload = false
            }
        )
    }

    kepandaian: string = 'teknis'
    pelamar_keahlian2: string = ''
    tingkat_keahlian2: string = ''
    simpanSkill() {
        let param: Pelamar_keahlian = {
            pelamar_keahlian: this.pelamar_keahlian2,
            tingkat_keahlian: this.tingkat_keahlian2,
            kategori_keahlian: this.kepandaian,
            id_pelamar: this.id_pelamar,
        };
        this.direktoriPelamarKeahlianService.postDirektoriPelamarKeahlians(param).subscribe(
            data=> {
                this.getKeahlian()
                this.toogleuploadSkill = false
            }
        )
    }

    getKeahlian() {
        this.direktoriPelamarKeahlianService.getDirektoriPelamarKeahlians(JSON.stringify({ id_pelamar: this.id_pelamar })).subscribe(
            data => {
                this.dataSkill = data.data['teknis']
                this.dataBahasa = data.data['bahasa']
            }
        )
    }

    delete(id) {
        this.spinner.show()
        this.direktoriPelamarKeahlianService
            .deleteDirektoriPelamarKeahliansId(id)
            .subscribe(data => {
                this.getKeahlian()
                this.spinner.hide()
            })
    }

    batalDelete() {
        this.nzMessageService.info('click cancel')
    }



}
