import { Pelamar_keahlian } from 'src/app/sdk/hr/models/pelamar-_keahlian';
import { Component, OnInit } from '@angular/core';
import { DirektoriPelamarKeahlianService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PelamarServiceService } from '../../service/pelamar-service.service';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router } from '@angular/router';
import { FormControl, Validators } from '@angular/forms';

@Component({
    selector: 'app-add',
    templateUrl: './add.component.html',
    styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

    constructor(
        private direktoriPelamarKeahlianService: DirektoriPelamarKeahlianService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pelamarServiceService: PelamarServiceService,
        private pesan: PesanService,
        private router: Router,
    ) { }

    id_pelamar: string = ''
    dataKeahlian: Pelamar_keahlian

    ngOnInit() {
        this.pelamarServiceService.getIdPelamar().subscribe(data => {
            this.id_pelamar = data
        })
        this.form()
    }

    dataForm: FormGroupTypeSafe<Pelamar_keahlian>
    form() {
        this.dataForm = this.fb.group<Pelamar_keahlian>({
            pelamar_keahlian: new FormControl('', [Validators.required]),
            kategori_keahlian: new FormControl(''),
            sertifikat: new FormControl(''),
            tingkat_keahlian: new FormControl(''),
            id_pelamar: new FormControl(this.id_pelamar),
        })
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.dataKeahlian = this.dataForm.value
        this.direktoriPelamarKeahlianService
            .postDirektoriPelamarKeahlians(this.dataKeahlian)
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/detail-pelamar', this.id_pelamar, 'keahlian', 'list'])
    }

}
